import React, { useRef, useState } from 'react';
import PropTypes from 'prop-types';

// Const & utils
import { screenMax } from '../../constants/theme';

// Components
import { StyledCarousel, StyledCarouselControls } from './styles';
import Slider from 'react-slick';
import './slick.css';
import './slick-theme.css';

import { Button, LeftArrowIcon, RightArrowIcon } from '../../components';

const CarouselControls = ({
  reversed,
  currentPage,
  totalPages,
  onPrevClick = () => {},
  onNextClick = () => {},
  ...props
}) => {
  return (
    <StyledCarouselControls>
      <Button
        type="button"
        onClick={onPrevClick}
        variant="link"
        reversed={reversed}
        aria-label="Previous"
        {...props}>
        <LeftArrowIcon />
      </Button>
      <ul>
        <li>{currentPage}</li>
        <li>/</li>
        <li>{totalPages}</li>
      </ul>
      <Button
        type="button"
        onClick={onNextClick}
        variant="link"
        aria-label="Next"
        reversed={reversed}
        {...props}>
        <RightArrowIcon />
      </Button>
    </StyledCarouselControls>
  );
};

export const Carousel = ({
  title = 'Explore Other Services',
  subtitle = 'Services & Products',
  children,
  reversed,
  overrideSettings,
  ...props
}) => {
  const [currentPage, setCurrentPage] = useState(1);
  const [totalPages, setTotalPages] = useState(0);
  const slider = useRef(null);

  const settings = {
    arrows: false,
    infinite: true,
    speed: 500,
    slidesToShow: 3,
    slidesToScroll: 3,
    initialSlide: 0,
    onReInit: () => {
      setTotalPages(
        Math.ceil(
          slider.current.innerSlider.props.children.length /
            slider.current.innerSlider.props.slidesToShow,
        ),
      );
    },
    afterChange: (currentSlide) => {
      setCurrentPage(
        Math.ceil(
          currentSlide / slider.current.innerSlider.props.slidesToShow,
        ) + 1,
      );
    },
    responsive: [
      {
        breakpoint: screenMax.sm,
        settings: { slidesToShow: 2, slidesToScroll: 2 },
      },
      {
        breakpoint: screenMax.xs,
        settings: { slidesToShow: 1, slidesToScroll: 1, variableWidth: true },
      },
    ],
    ...overrideSettings
  };

  return (
    <>
      <StyledCarousel {...props}>
        {subtitle && <span>{subtitle}</span>}
        {title && <h2>{title}</h2>}
        <CarouselControls
          reversed={reversed}
          currentPage={currentPage}
          totalPages={totalPages}
          onNextClick={() => slider.current.slickNext()}
          onPrevClick={() => slider.current.slickPrev()}
        />
        <Slider ref={slider} {...settings}>
          {children}
        </Slider>
      </StyledCarousel>
    </>
  );
};
Carousel.propTypes = {
  title: PropTypes.string,
  subtitle: PropTypes.string,
  reversed: PropTypes.bool,
  overrideSettings: PropTypes.object,
};
